.PageFiveContainer{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding-top: 10px;





    .sectionOne{
        min-width: 100vw;
        max-height: 100px;   
        min-height: 100px;
        background-image: url('../../../img/home/Fondo4-1.webp');
        background-repeat: no-repeat;
        display:  flex;
        justify-content: center;
        background-size: 150%;
        background-position-x: center;
        background-position-y: center;
    }
    .sectionTwo{
        min-width: 100vw;
        min-height: 35vh;
        background-image: url('../../../img/home/Fondo4-2.webp');
        background-repeat: no-repeat;
        display:  flex;
        background-size: cover;
        background-position-y: 0vh;
        background-position-x: center;
    }
    .sectionThree{
        min-width: 100vw;
        min-height: 20vh;
        background-color: white;
        background-image: url('../../../img/carruseles/img4-1.svg');
        background-repeat: no-repeat;
        display:  flex;
        background-size: 110vw;
        background-position-y: 5vh;
        background-position-x: 5vw;
    }
    .sectionFour{
        min-width: 100vw;
        min-height: 30vh;
        background-repeat: no-repeat;
        display:  flex;
        justify-content: center;
        background-size: 130vw;
        background-position-y: 0vh;
        background-position-x: -20vw;



        .ImageOne{
            z-index: 3;
            position: absolute;
            min-width: 100vw;
            min-height: 35vh;
            background-image: url('../../../img/home/Fondo4-01.webp');
            background-size: cover;

        }
        .ImageTwo{
            z-index: 2;
            position: absolute;
            min-width: 100vw;
            min-height: 35vh;
            background-image: url('../../../img/home/Fondo4-02.webp');
            background-size: 140vw;
            background-position-x: 0vw;
        }
        .ImageThree{
            z-index: 1;
            position: absolute;
            min-width: 100vw;
            min-height: 35vh;
            background-image: url('../../../img/home/Fondo4-03.webp');
            background-size: 130vw;
            background-position-x: 0vw;
        }

    
 

        ul{
            z-index: 4;
            min-width: 60vw;
            margin-top: 40vw;
            min-height: 5vh;
            position: absolute;
            display: flex;
            justify-content: space-around;
            align-items: flex-end;
            li{
                list-style: none;
                min-width: 15px;
                max-width: 15px;
                min-height: 15px;
                max-height: 15px;
                border-radius: 10px;
                border: 2px solid #C68738 ;
                background-color: aliceblue;
            }
        }


    }
      
}



