.containerGalleryPageOne{
    display: flex;
    min-width: 100vw;
    flex-wrap: wrap;
    justify-content: center;
    background-color: #F2F2F2;

    .title{
        border: 0px;
        padding: 0px;
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 85vw;                
        min-height: 0vh;
        margin-top: 5vh;
        label{
            border: 0px;
            padding: 0px;
            display: flex;
            justify-content: center;
            align-items: center;
            min-width: 85vw;    
            min-height: 15vh;            
            font-size: 35px;
            color: #C68738;
        }
    }
    .galleryContent{
        margin-top: 5px;
        min-width: 85vw;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        .imageOne{
            min-width: 42.5vw;
            min-height: 60vw;
            background-image: url('../../../img/gallery/img1.svg');
            background-size: cover;
        }
        .imageTwo{
            margin-left: 5px;
            min-width: 42.5vw;
            min-height: 60vw;
            background-image: url('../../../img/gallery/img2.svg');
            background-size: cover;
        }
        .imageThreeDesktop{
            display: none;
        }
        .imageThree{
            margin-top: 5px;
            min-width: 85vw;
            min-height: 60vw;
            background-image: url('../../../img/gallery/img3.svg');
            background-size: cover;
        }
        .imageFour{
            margin-top: 5px;
            min-width: 85vw;
            min-height: 60vw;
            background-image: url('../../../img/gallery/img4.svg');
            background-size: cover;
        }
    }
    .galleryContentDos{
       
        margin-top: 5px;
        min-width: 85vw;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        display: none;
        .imageOne{
            min-width: 42.5vw;
            min-height: 60vw;
            background-image: url('../../../img/gallery/img5.svg');
            background-size: cover;
        }
        .imageTwo{
            margin-left: 5px;
            min-width: 42.5vw;
            min-height: 60vw;
            background-image: url('../../../img/gallery/img6.svg');
            background-size: cover;
        }
        .imageThree{
            margin-top: 5px;
            min-width: 85vw;
            min-height: 60vw;
            background-image: url('../../../img/gallery/img7.svg');
            background-size: cover;
        }
        .imageFour{
            min-width: 85vw;
            min-width: 42.5vw;
            min-height: 60vw;
            background-image: url('../../../img/gallery/img8.svg');
            background-size: cover;
        }
        .imageFive{
            margin-left: 5px;
            min-width: 42.5vw;
            min-height: 60vw;
            background-image: url('../../../img/gallery/img9.svg');
            background-size: cover;
        }
    }
    .More{
        min-width: 85vw;
        max-width: 85vw;
        min-height: 15vh;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        .buttonMore{
            min-width:85vw;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            label{
                min-width:85vw;
                text-align: center;
                color: #CCA549;
                font-family: Montserrat;
                font-weight: 600;
            }
            img{
                margin-right: 15vw;
                margin-left: 15vw;
                min-width: 5vw;
    
            }
            
        }
      
    }


}



